export default
`.example__target {
    height: 350px;
    padding: 10px;
    position: relative;
    border: 1px solid #ccc;
    border-radius: 4px;
    transition: border .2s ease-in-out;
    text-align: center;
}

.example__target: hover {
    border-color: #337ab7;
}
`;




